.new-inspection {
  overflow-y: auto;
  overflow-x: hidden;
}

.new-inspection form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0 40px 0;
}

.new-inspection-text {
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 33px;
  text-align: center;
  color: #000000;
  max-width: 300px;
  margin: 0 0 5px 0;
}

.new-inspection-camera {
  width: 100%;
  max-width: 300px;
  margin: 0 0 20px 0;
}

.MuiOutlinedInput-notchedOutline {
  border: 0 !important;
}

.MuiFormLabel-root.Mui-focused {
  color: #7C939C;
}